/*whole website */

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

$SansSerif: "Montserrat", sans-serif;

p,b{
  white-space:normal;
}
a { text-decoration: none; }

body{
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
  /*background-color: #F0EEE7;*/
}

.background_img{
  position: fixed;
  top: -2%;
  left: -2%;
  min-width: 104%;
  min-height: 104%;
  object-fit: cover;
  filter: blur(5px) opacity(95%) grayscale(100%);
  z-index: -1;
}

/*sidebar*/

.sidebar_box{
  background-color: rgba(0,0,0,0.9);
  flex: 20%;
  height: 100vh;
  border: 0px;
  margin: 0;
  padding: 0;
  position: sticky;
  top: 0px;
  background-size: cover;
  z-index: 2;
}
.logo{
  height: 010vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5%;
  padding: 0;
  border: 0px;
  border-left: 0.5vw solid #ffffff;
  border-top: 0.5vw solid #ffffff;
  z-index: 2;
}
.logo a{
  position: absolute;
  font: 700 4vw/0.9 "Montserrat", sans-serif; /*40px*/
  text-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2),
  1px -1px 1px rgba(0, 0, 0, 0.2),
  1px 1px 1px rgba(0, 0, 0, 0.2),
  -1px -1px 1px rgba(0, 0, 0, 0.2);
  text-align: justify;
  color: #ffffff;
  letter-spacing: 5px;
  left: 15%;
  z-index: 2;
}
.logo:hover a{
  font: 700 3.8vw/0.9 "Montserrat", sans-serif; /*40px*/
  transition: .3s ease;
}

