/*navmenu*/
.nav_menu_box{
width: 100%;
  padding: 0 6% 0 5%;
  min-width: 170px;
}
.nav_menu{
  display: block;
  width: 100%;
  font: 400 16px/0.9 "Montserrat", sans-serif;
  color: #f3c0b6  ;
  letter-spacing: 1px;
  padding-top: 15px;
  padding-right: 0px;
  text-align: right;
  transition: .3s ease;
}
.nav_menu .small{
  font-size: 12px;
  color: #b38d84;
  transition: .3s ease;
}
.nav_menu_highlight{
  display: block;
  width: 100%;
  font: 400 16px/0.9 "Montserrat", sans-serif;
  color: #ffffff;
  letter-spacing: 1px;
  padding-top: 15px;
  padding-right: 20px;
  text-align: right;
  transition: .3s ease;
}
.nav_menu_highlight .small{
  font-size: 12px;
  transition: .3s ease;
}
.nav_menu:hover{
  color: #ffffff;
  padding-right: 20px;
  transition: .3s ease;
}

/*page body*/
.main_box{
  flex: 80%;
  border: 0px;
  display: flex;
  flex-direction: row;
  margin: 0 0 0 0;
  padding: 0.5%;
}
.large_row{
  flex:75%;
  display: flex;
  flex-direction: column;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  align-items: center;
}
.nested_row_div{
  flex:100%;
  display: flex;
  flex-direction: row;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}
.thin_row{
  flex:25%;
  display: flex;
  flex-direction: column;
  margin: 0 0 0 0;
  padding: 0 0 0 0 ;
  align-items: center;
}
.nested_row_div .thin_row{
  flex:34%;
}

/*image container*/
.image_container {
  display: block;
  position: relative;
  width: 97%;
  margin: 0 1.5% 3% 1.5%;
}
.image_container img {
  width:100%; 
  height: 100%;
}
.overlay {
  position: absolute; 
  bottom: 20px;
  left: 10px;
  padding: 5px;
  z-index: 2;
  background: #000000;
  transition: .3s ease;
  opacity:0;
  font: 18px "Montserrat", sans-serif;
  color: #ffffff;
}/*
.image_container:hover .overlay {
  opacity: 1;
}
.image_container:hover img {
  filter:  brightness(0.6);
  transition: .3s ease;
}*/

